import React, { useState, useEffect } from "react";
import useLocalStorage from './useLocalStorage.js';

export default ({ search, onFavoriteClick }) => {
  const [name, setname] = useState()
  const [favorites, setFavorites] = useLocalStorage('favorites', [])

  const handleAddFavoriteClick = () => {
    if (!name) {
      return
    }

    const id = Math.random().toString(36).substr(2, 9)
    const favorite = { name, search, id }

    setFavorites(favorites.concat(favorite))
  }

  return (
    <div>
      <b style={{ display: 'block' }}>Favorites</b>

      {favorites.map(favorite =>
        <div key={favorite.id}>
          <a href="#" onClick={() => onFavoriteClick(favorite.search)}>{favorite.name}</a>
        </div>
      )}

      Name:{" "}
      <input
        style={{ width: 150 }}
        value={name}
        onChange={e => setname(e.target.value)}
      />
      <br />
      <button onClick={handleAddFavoriteClick}>Add</button>
    </div>
  );
}
