import React from "react";
import ReactDOM from "react-dom";

import Favorites from './Favorites.js';
import IssuesTable from './IssuesTable.js';
import Settings from './Settings.js';

import USERS from './users.js';
import searchGithubIssues from './searchGithubIssues.js';

class App extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      sidebarVisible: true,
      page: 1,

      org: "edlio",
      repo: "",
      closed: "",
      merged: "",
      created: "",
      assignee: "",
      author: "",
      status: "open",
      is: "issue",
      sort: "",
      order: "desc"
    };
  }

  componentDidMount() {
    this.search()
  }

  get query() {
    const {
      status, repo, closed, merged, is, org, assignee, created, author
    } = this.state;

    let query = "";

    if (status) {
      query += `+is:${status}`;
    }

    if (closed) {
      query += `+closed:${closed}`;
    }

    if (merged) {
      query += `+merged:${merged}`;
    }

    if (created) {
      query += `+created:${created}`;
    }

    if (assignee) {
      query += `+assignee:${assignee}`;
    }

    if (author) {
      query += `+author:${author}`;
    }

    if (repo) {
      query += `+repo:${org}/${repo}`;
    } else {
      query += `+org:${org}`;
    }

    if (is) {
      query += `+is:${is}`;
    }

    return query;
  }

  get issueCountPerPage() {
    return 20
  }

  get sortFields() {
    return ["created", "updated", "comments", "reactions", "interactions"];
  }

  get currentSearch() {
    const {org, repo, closed, merged, created, assignee, author, status, is, sort, order} = this.state

    return { org, repo, closed, merged, created, assignee, author, status, is, sort, order }
  }

  async handleFavoriteClick(search) {
    await this.setState({ page: 1 })
    await this.setState(search)
    await this.search()
  }

  async handleSearch() {
    await this.setState({ page: 1 });

    this.search();
  }

  async search() {
    const { page, sort, order } = this.state;
    const githubToken = localStorage.getItem('github_token')
    const results = await searchGithubIssues(this.query, page, sort, order, githubToken, this.issueCountPerPage)

    this.setState({ results });
  }

  render() {
    const {
      sort,
      order,
      status,
      author,
      results,
      repo,
      closed,
      merged,
      is,
      page,
      org,
      assignee,
      created,
      sidebarVisible
    } = this.state;

    return (
      <div
        style={{
          fontSize: 13,
          fontFamily:
            "-apple-system,BlinkMacSystemFont,Segoe UI,Helvetica,Arial,sans-serif,Apple Color Emoji,Segoe UI Emoji,Segoe UI Symbol",
          display: "flex"
        }}
      >
        <div style={{ flexGrow: 1 }}>
          <IssuesTable
            results={results}
            page={page}
            issueCountPerPage={this.issueCountPerPage}
            onSearchChange={async search => {
              await this.setState(search)
              this.search()
            }}
          />
        </div>

        <div
          style={{
            position: 'fixed',
            top: 0,
            right: 0,
            display: !sidebarVisible ? 'block' : 'none',
          }}
        >
          <button onClick={() => this.setState({ sidebarVisible: true})}>show search</button>
        </div>


        <div
          style={{
            display: sidebarVisible ? 'block' : 'none',
            padding: 5,
            background: "#e0e0e0"
          }}
        >
          <div>
            <b>Github Search</b> <button onClick={() => this.setState({ sidebarVisible: false})}>hide</button>
          </div>
          Org:{" "}
          <input
            value={org}
            onChange={e => this.setState({ org: e.target.value })}
          />
          <br />
          Repo:{" "}
          <input
            value={repo}
            onChange={e => this.setState({ repo: e.target.value })}
          />
          <br />
          Opened:{" "}
          <input
            style={{ width: 150 }}
            value={created}
            onChange={e => this.setState({ created: e.target.value })}
          />
          <br />
          Closed:{" "}
          <input
            style={{ width: 150 }}
            value={closed}
            onChange={e => this.setState({ closed: e.target.value })}
          />
          <br />
          Merged:{" "}
          <input
            style={{ width: 150 }}
            value={merged}
            onChange={e => this.setState({ merged: e.target.value })}
          />
          <br />
          Type:{" "}
          <select
            value={is}
            onChange={e => this.setState({ is: e.target.value })}
          >
            <option value="" />
            <option value="issue">issue</option>
            <option value="pr">pr</option>
          </select>
          <br />
          Status:{" "}
          <select
            value={status}
            onChange={e => this.setState({ status: e.target.value })}
          >
            <option value="" />
            <option value="open">open</option>
            <option value="closed">closed</option>
            <option value="merged">merged</option>
            <option value="unmerged">unmerged</option>
          </select>
          <br />
          Assignee:{" "}
          <select
            value={assignee}
            onChange={e => this.setState({ assignee: e.target.value })}
          >
            <option value="" />
            {USERS.map(user => (
              <option value={user}>{user}</option>
            ))}
          </select>
          <br />
          Author:{" "}
          <select
            value={author}
            onChange={e => this.setState({ author: e.target.value })}
          >
            <option value="" />
            {USERS.map(user => (
              <option value={user}>{user}</option>
            ))}
          </select>
          <br />
          Sort:{" "}
          <select
            value={sort}
            onChange={e => this.setState({ sort: e.target.value })}
          >
            <option value="" />
            {this.sortFields.map(sort => (
              <option value={sort}>{sort}</option>
            ))}
          </select>
          <br />
          Order:{" "}
          <select
            value={order}
            onChange={e => this.setState({ order: e.target.value })}
          >
            <option value="desc">desc</option>
            <option value="asc">asc</option>
          </select>
          <br />
          <button onClick={this.handleSearch.bind(this)}>Search</button>

          <hr />
          <Favorites
            search={this.currentSearch}
            onFavoriteClick={this.handleFavoriteClick.bind(this)}
          />

          <hr />
          <Settings />
        </div>
      </div>
    );
  }
}

const rootElement = document.getElementById("root");
ReactDOM.render(<App />, rootElement);

