import React from "react";

import Avatar from './Avatar.js';
import Label from './Label.js';

import { repoName, date } from './helpers.js';

export default class extends React.Component {
  get pageCount() {
    const { results, issueCountPerPage } = this.props;

    const allPages = Math.ceil(results.total_count / issueCountPerPage);

    return Math.min(allPages, 1000 / issueCountPerPage);
  }

  async handlePageClick(page) {
    this.props.onSearchChange({ page });
  }

  async handleAuthorClick(user) {
    this.props.onSearchChange({ author: user.login, page: 1 });
  }

  async handleAssigneeClick(user) {
    this.props.onSearchChange({ assignee: user.login, page: 1 });
  }

  async handleRepoClick(repo) {
    this.props.onSearchChange({ repo, page: 1 });
  }

  render() {
    const { results, page, issueCountPerPage } = this.props;

    return (
      <div>
        {results && results.message}

        {results && results.items && (
          <div>
            <table style={{ width: '100%' }}>
              <tr>
                <th>Results {results.total_count}</th>
                <th>Author</th>
                <th>Assignees</th>
                <th>Created</th>
                <th>Updated</th>
                <th>Closed</th>
              </tr>
              {results.items.map(item => (
                <tr>
                  <td>
                    <b>{item.pull_request && "PR "}</b>{" "}
                    <a
                      href="#"
                      onClick={() => this.handleRepoClick(repoName(item))}
                    >
                      {repoName(item)}
                    </a>
                    #{item.number}{" "}
                    <a
                      style={{ color: item.state === "closed" && "gray" }}
                      target="_blank"
                      href={item.html_url}
                    >
                      {item.title}
                    </a>{" "}
                    {item.comments > 0 && item.comments + ' ðŸ’¬'}
                    {item.labels.map(label => (
                      <Label label={label} />
                    ))}
                  </td>
                  <td>
                    <Avatar
                      user={item.user}
                      onClick={this.handleAuthorClick.bind(this)}
                    />
                  </td>
                  <td>
                    {item.assignees.map(assignee => (
                      <Avatar
                        user={assignee}
                        onClick={this.handleAssigneeClick.bind(this)}
                      />
                    ))}
                  </td>
                  <td>{date(item.created_at)}</td>
                  <td>{date(item.updated_at)}</td>
                  <td>{date(item.closed_at)}</td>
                </tr>
              ))}
            </table>

            {this.pageCount > 1 && (
              <div>
                {new Array(this.pageCount).fill(1).map((v, p) => (
                  <button onClick={() => this.handlePageClick(p + 1)}>
                    {page === p + 1 && ">"}
                    {p + 1}
                  </button>
                ))}
              </div>
            )}
          </div>
        )}
      </div>
    );
  }
}
