import React from "react";
import useLocalStorage from './useLocalStorage.js';

export default ({ search, onFavoriteClick }) => {
  const [githubToken, setGithubToken] = useLocalStorage('github_token')

  const handleTokenChange = (e) => {
    setGithubToken(e.target.value)
  }
  const handleClearTokenClick = () => {
    setGithubToken(null)
  }

  return (
    <div>
      <b style={{ display: 'block' }}>Settings</b>

      {!githubToken &&
        <div>
          <div>Github token:</div>
          <input
            style={{ width: 150 }}
            placeholder='paste'
            value={githubToken}
            onChange={handleTokenChange}
          /><br/>
          <a target='_blank' href='https://github.com/settings/tokens'>
            Create personal access token
          </a>
        </div>
      }

      {githubToken &&
        <button onClick={handleClearTokenClick}>
          Clear Github token
        </button>
      }
    </div>
  );
}
