export const repoName = issue => {
  const parts = issue.repository_url.split("/");
  return parts[parts.length - 1];
};

export const date = d => {
  if (d) {
    const options = { weekday: 'short', year: '2-digit', month: '2-digit', day: 'numeric' };

    return (new Intl.DateTimeFormat('en-US', options)).format(new Date(d))
  }
};
